
import { defineComponent } from 'vue'
import { useStore } from 'vuex'
import useFetching, { FetchStatus, ResponseFetching } from '@/hooks/useFetching'
import { IProductForm } from '@/types'
import PageCard from '@/components/PageCard.vue'
import ProductForm from '@/views/catalog/product/forms/ProductForm.vue'

export interface IData {
  model: IProductForm | null;
}

export default defineComponent({
  setup () {
    const store = useStore()

    const [saveForm, saveStatus, saveError] = useFetching((dataForm: IProductForm) => {
      return store.dispatch('product/createForm', dataForm)
    }) as ResponseFetching<Promise<IProductForm>>

    return {
      saveForm,
      saveStatus,
      saveError
    }
  },

  components: {
    PageCard,
    ProductForm
  },

  computed: {
    isLoading (): boolean {
      return this.saveStatus === FetchStatus.pending
    }
  },

  data (): IData {
    return {
      model: {
        manufacturerId: null,
        productStatusId: null,
        name: '',
        description: null,
        tags: null,
        metaTitle: '',
        metaDescription: null,
        metaKeyword: null,
        model: '',
        sku: '',
        location: '',
        price: '0.00',
        image: '',
        quantity: 999,
        minimum: 1,
        points: 0,
        weight: '0.00000000',
        weightClassId: null,
        length: '0.00000000',
        width: '0.00000000',
        height: '0.00000000',
        lengthClassId: null,
        isShipping: false,
        isSubtract: false,
        sortOrder: 0,
        isActive: true,
        availableAt: new Date().toISOString(),
        filters: [],
        categories: [],
        relates: [],
        images: [],
        rewards: [],
        discounts: [],
        attributes: [],
        options: [],
        specials: []
      }
    }
  },

  methods: {
    handleBack () {
      this.$router.push({ name: 'product' })
    },

    async handleSubmit () {
      const data: IProductForm = await this.saveForm(this.model)
      if (typeof data !== 'undefined') {
        this.model = data
      }

      if (this.model?.id) {
        this.$router.push({ name: 'product-update', params: { id: this.model.id } })
      }
    }
  }
})
